.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

 .title{
    color:  rgba(218, 165, 32, 1);
    font-size: 50px;
    animation: flipTitle 1.5s;
 }

 @keyframes flipTitle{
   from{
      transform: rotateX(90deg);
   }
   to{
      transform: rotateX(0deg);
   }
 }

 .container-input{
   background-color: rgb(7, 1, 70);
   margin: 35px;
   padding: 15px;
   display: flex;
   border-radius: 20px;
 }
 .container-input input{
   background: transparent;
   border: none;
   font-size: 20px;
   color: white;
   outline: none;
   margin-right: 8px;
 }

 .button-search{
   background-color: transparent;
   border: none;
   cursor: pointer;
   display: flex;
   align-items: center;
   transition: transform 0.5s;
 }
 .container-input input::placeholder{
   color:  rgba(218, 165, 32, 1);
 }
 
 .main{
   color:#000;
   display: flex;
   align-items: center;
   flex-direction: column;
   background-color: white;
   width: 250px;
   height: 190px;
   border-radius: 8px;
 }

 .button-search:hover{
   transform: scale(1.2);
 }

 .main h2{
   margin: 16px 0;
   font-size: 20px;
 }
 .main span{
   margin: 5px auto;
   padding: 5px auto;
   font-size: 16px;
   font-weight: bold;
 }

 @media(max-width:620px){
  .title{
   font-size: 30px;
  }
  .main h2{
   font-size: 16px;
  }
  .main span{
   font-size: 12px;
  }
  .main{
   width: 80%;
   min-height: 250px;
  }
 }